.FormInput {
    width: 100%;
    height: 50px;
    border: none;
    border-radius: 5px;
    outline: none;
    font-size: 18px;
    margin-top: 15px;
    padding-left: 15px;
    padding-right: 10px;
    box-shadow: 0 6px 6px -6px #ffffff;
}

.FormInputContainer {
    position: relative;
}

.InputLabel {
    position: absolute;
    top: 25px;
    left: 10px;
}